import {
	mapState,
	mapMutations
} from 'vuex'
import uniBadge from '@/components/uni-badge/uni-badge.vue'
import dateUtils from '../../common/util.js'
export default {
	components: {
		uniBadge
	},
	computed: mapState(["header"]),
	data() {
		return {
			"id": "",
			"date": Date(),
			index: 0,
			nowCity: "",
			multiArray: [
				['湖南省', '湖北省'],
				['长沙市', '益阳市'],
				['芙蓉区', '天心区', '岳麓区', '开福区', '雨花区', '望城区', '浏阳市', '宁乡市', '长沙县']
			],
			multiIndex: [0, 0, 2],
			health: 0,
			beenToHubei: false,
			beenNCT: false
		}
	},
	methods: {
		...mapMutations(['filled']),
		location() {
			let data = this;
			uni.chooseLocation({
				success: function(res) {
					console.log('位置名称：' + res.name);
					console.log('详细地址：' + res.address);
					data.nowCity = res.address.toString();
					data.$forceUpdate();
					//this.nowCity = res.address.toString();

				}
			});

		},
		bindMultiPickerColumnChange: function(e) {
			console.log('修改的列为：' + e.detail.column + '，值为：' + e.detail.value)
			this.multiIndex[e.detail.column] = e.detail.value
			switch (e.detail.column) {
				case 0: //拖动第1列
					switch (this.multiIndex[0]) {
						case 0:
							this.multiArray[1] = ['长沙市', '益阳市']
							this.multiArray[2] = ['芙蓉区', '天心区', '岳麓区', '开福区', '雨花区', '望城区', '浏阳市', '宁乡市', '长沙县']
							break
						case 1:
							this.multiArray[1] = ['武汉市', '咸宁市', '黄冈市']
							this.multiArray[2] = ['江岸区', '江汉区', '硚口区', '汉阳区', '武昌区', '青山区', '洪山区', '东西湖区']
							break
					}
					this.multiIndex.splice(1, 1, 0)
					this.multiIndex.splice(2, 1, 0)
					break
				case 1: //拖动第2列
					switch (this.multiIndex[0]) { //判断第一列是什么
						case 0:
							switch (this.multiIndex[1]) {
								case 0:
									this.multiArray[2] = ['芙蓉区', '天心区', '岳麓区', '开福区', '雨花区', '望城区', '浏阳市', '宁乡市', '长沙县']
									break
								case 1:
									this.multiArray[2] = ['资阳区', '赫山区', '沅江市', '南县', '桃江县', '安化县', '大通湖管理区', '高新技术产业园区']
									break
							}
							break
						case 1:
							switch (this.multiIndex[1]) {
								case 0:
									this.multiArray[2] = ['江岸区', '江汉区', '硚口区', '汉阳区', '武昌区', '青山区', '洪山区', '东西湖区']
									break
								case 1:
									this.multiArray[2] = ['咸安区', '嘉鱼县', '通城县', '崇阳县', '通山县', '赤壁市']
									break
								case 2:
									this.multiArray[2] = ['黄州区', '麻城市', '武穴市', '团风县', '浠水县', '罗田县', '英山县', '蕲春县', '黄梅县', '红安县', '龙感湖管理区']
									break
							}
							break
					}
					this.multiIndex.splice(2, 1, 0)
					break
			}
			this.$forceUpdate()
		},
		formSubmit: function(e) {
			console.log('form发生了submit事件，携带数据为：' + JSON.stringify(e.detail.value))
			var formdata = e.detail.value
			uni.showLoading({
				title: "加载中..."
			})
			let data = JSON.stringify(formdata)
			this.$http.post("/mem/health",data).then(res=>{
				console.log(res.data);
				if (res.data.META.SUCCESS) {
					uni.showToast({
						title: "您当日已填写",
						icon: "success",
						duration: 1500
					})
					this.filled(false);
					setTimeout(() => uni.navigateBack({}), 2000)
				} else {
					uni.showToast({
						title: res.data.META.MESSAGE,
						icon: "none"
					})
				}
				uni.hideLoading()
			}).catch(err=>uni.showToast({
					title: "请求出错:" + err.toString(),
					icon: "none"
				}))
			
		/* 	uni.request({
				url: "https://ser1.itrobot.site/maxrest/rest/mem/health",
				method: "POST",
				header: {
					"MAXAUTH": this.header
				},
				data: JSON.stringify(formdata),
				success: (res) => {
					console.log(res.data);
					if (res.data.META.SUCCESS) {
						uni.showToast({
							title: "您当日已填写",
							icon: "success",
							duration: 1500
						})
						this.filled(false);
						setTimeout(() => uni.navigateBack({}), 2000)
					} else {
						uni.showToast({
							title: res.data.META.MESSAGE,
							icon: "none"
						})
					}
				},
				complete() {
					uni.hideLoading()
				}
			}); */

			/* uni.showModal({
				content: '表单数据内容：' + JSON.stringify(formdata),
				showCancel: false
			}); */
		},
		formReset: function(e) {
			console.log('清空数据')
		}
	},
	onLoad(e) {
		this.date = new Date();

		console.log(e)
		this.id = e.id;
	}
}
